body{
  margin:0;

  .main-navigation{
    background:#fff;
    margin-bottom: 30px;

    h1{
      font-size:1.3em;
      display:inline-block;
      margin-bottom: 0;
      margin-top: 15px;

      a{
        margin-bottom:0;
        border-bottom:none;
      }
    }

    ul{
      list-style:none;
      float:right;
      margin-bottom: 0;

      li{
        display:inline-block;
        margin:15px 15px 15px 0;

        a{
          text-decoration:none;
          margin-bottom:0;
          border-bottom:none;
        }
      }
    }
  }
}
