button{
  margin:30px auto;
  border:none;
  background:$med;
  color:#fff;
  transition:all 300ms ease-in-out;
  text-transform:none;
  font-weight:400;

  &:hover{
    background:$hover;
    color:#fff;
  }

  &.ghost-bottom{
    margin: 0;
    background: transparent;
    color: #3A7CA5;
    font-weight: 600;
    padding: 0;
    border-bottom: 1px solid;
    font-size: 13px;
    border-radius: 0;

    &:hover{
      background:transparent;
      color:$hover;
    }
  }

  &.ghost{
    background: transparent;
    color: $med;
    border: 1px solid $med;
    margin: 0;
    padding: 0 10px;
  }

}
