body{
  font-family: 'Open Sans', sans-serif;
  font-weight:400;
  color: $font;
}

strong, h1, h2, h3, h4, h5, h6{
  font-weight:600;
}

.light{
  font-weight:300;
}

a{
  color:$dark;
  transition:all 300ms ease-in-out;
  text-decoration:none;
  padding-bottom:4px;
  border-bottom:1px solid;
}

a:hover{
  color:$hover;
}
