input[type="date"], .ember-select, input[type="textarea"]{
  height: 38px;
  padding: 6px 10px;
  background-color: #fff;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

input[type="textarea"]{
    min-height:120px;
}

input{
  display:block;
  margin:15px 0;
  width:100%;
}
.type input, .type label{
  display:inline-block;
  display: inline-block;
  width: auto;
  margin-top: 0;
}
