.all-events{

  .colum, .columns{
    margin-left:0;
  }

  ul{
    list-style:none;

    li{
      border-left:3px solid;
      background:#fff;
      margin-right: 10px;
      box-shadow: 2px 3px 8px #ccc;

      &.Tech{
        color:$dark;
      }

      &.Social{
        color:$med;
      }

      &.Friends{
        color:$hover;
      }

      .meta-info{
        padding:10px 8px;
        margin-left:20px;
        color:$font;

        p{
          margin-bottom:1em;

          strong{
            font-size:.8em;
          }
        }
      }
    }
  }
}
