.single-event{
  position:relative;

  .event-details{
    .ghost-bottom{
      top: 0;
      right: 0;
      position: absolute;
      margin: 0;
    }
  }

  .column, .columns{
    margin-left:0;
  }
}

.update-modal{
  background: rgba(255, 255, 255, 0.9);
  overflow: auto;
  position: absolute;
  top: 0;
  left: 0;
  min-height: 100%;
  z-index: 1;
  padding: 30px;
}
